// eslint-disable-next-line import/no-extraneous-dependencies
import {
    RouteLocationInterface,
    RouteHttpVerb,
    RouteRequestInterface,
    RouteParamsInterface,
    RouteResponseInterface
} from "@devowl-wp/utils";

export const locationRestFeedbackPost: RouteLocationInterface = {
    path: "/feedback/:slug",
    method: RouteHttpVerb.POST
};

export interface RequestRestFeedbackPost extends RouteRequestInterface {
    reason: string;
    note: string;
}

export interface ParamsRestFeedbackPost extends RouteParamsInterface {
    slug: string;
}

export type ResponseRestFeedbackPost = RouteResponseInterface;
